<template>
  <div>
    <head-factory :active='2' :user="user_info" :powerMenu="power"></head-factory>
    <student-register-manage-factory :dataInfo="studentInfo" :user_info="user_info"></student-register-manage-factory>
  </div>
</template>

<script>
import headFactory from '../../components/head/headFactory/headFactory'
import studentRegisterManageFactory from '../../components/body/studentRegisterManage/studentRegisterManageFactory/studentRegisterManageFactory'
import axModule from 'axios'

export default {
  name: 'studentRegisterManagePage',
  components: {
    studentRegisterManageFactory,
    headFactory
  },
  created () {
    if (this.$cookies.isKey('user_info')) {
      this.user_info = this.$cookies.get('user_info')
    } else {
      this.$router.push({path: '/login'})
    }
    this.getAllStudentsRegister()
  },
  data () {
    return {
      studentInfo: [],
      user_info: {},
      power: true
    }
  },
  methods: {
    getAllStudentsRegister () {
      axModule.get(this.GLOBAL.host + '/getStudentRegisterInfo', {
        params: {
          user_id: this.user_info.user.userId,
          user_password: this.user_info.user.userPassword,
          classNo: this.$store.state.storeCurrentClassNo,
          register_time: this.$store.state.storeCurrentRegisterTime
        }
      }).then(res => {
        console.log(res)
        if (res.status === 200) {
          this.studentInfo = res.data
          // setTimeout(this.getAllStudentsRegister, 2000)
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



