<template>
  <el-table
    :data="dataInfo"
    style="width: 100%;" border>
    <el-table-column
      fixed
      type="index"
      header-align="center"
      align="center"
      width="40"
    >
      <template slot="header">
        <span style="font-size: 15px;">序号</span>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      prop="pictureUrl"
      align="center"
      header-align="center"
      width="100"
    >
      <template slot="header">
        <span style="font-size: 15px;">照片</span>
      </template>
      <template slot-scope="pic">
        <el-image :src="pic.row.pictureUrl" style="width: 80px;height: 100px;" fit="cover"></el-image>
      </template>

    </el-table-column>
    <el-table-column
      fixed
      prop="studentNumber"
      align="center"
      header-align="center"
      min-width="70"
    >
      <template slot="header">
        <span style="font-size: 15px;">学号</span>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      prop="studentName"
      align="center"
      header-align="center"
      min-width="70"
    >
      <template slot="header">
        <span style="font-size: 15px;">姓名</span>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      prop="className"
      align="center"
      header-align="center"
      min-width="100"
    >
      <template slot="header">
        <span style="font-size: 15px;">班级</span>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      prop="lockSituation"
      align="center"
      header-align="center"
      min-width="90"
    >
      <template slot="header">
        <span style="font-size: 15px;">锁屏情况</span>
      </template>
    </el-table-column>
    <el-table-column
      prop="situation"
      align="center"
      header-align="center"
      min-width="110"
    >
      <template slot="header">
        <span style="font-size: 15px;">考勤情况</span>
      </template>
      <template slot-scope="row_item">

        <el-select v-model="row_item.row.situation" placeholder="请选择" style="width: 150px;">
          <el-option
            v-for="item_choose in chooseOptions"
            :key="item_choose.value"
            :label="item_choose.label"
            :value="item_choose.value">
          </el-option>
        </el-select>
        <el-button class="el-button-local" type="primary" size="mini" @click="modifyStudentRegisterSit(row_item.row)">保存</el-button>
        <!--        <el-button class="el-button-local" @click="handleClick(scope.row)" type="primary" size="mini">锁屏</el-button>-->
        <!--        <el-button class="el-button-local" @click="handleClick(scope.row)" type="primary" size="mini">解锁</el-button>-->
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import axModule from 'axios'

export default {
  name: 'componentStudentRegisterManage',
  props: {
    dataInfo: Array,
    user_info: Object
  },
  data () {
    return {
      // tableData: {
      //   data_all: [{ // 表格数据
      //     pri_pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586324454011&di=b6e0f65310fc99c70d6866202101698e&imgtype=0&src=http%3A%2F%2Fwww.99danji.com%2Fupload%2F20129%2F2012092951724937.jpg', // 学生照片
      //     student_id: '2018750', // 学生学号
      //     student_name: '阮啟东', // 学生姓名
      //     class_belong: '软件2017-01', // 学生班级
      //     lock_state: '已锁屏', // 当前锁屏状况
      //     every_register_sit: '1'// 出勤状态码  1出勤 2迟到 3 请假 4缺勤
      //   },
      //   { // 第二个学生的所有信息
      //     pri_pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586324392292&di=07aa95cc97210222ab0222bfbf99bed1&imgtype=0&src=http%3A%2F%2Fgss0.baidu.com%2F-fo3dSag_xI4khGko9WTAnF6hhy%2Fzhidao%2Fpic%2Fitem%2F0dd7912397dda1446b11f863b7b7d0a20df486c3.jpg',
      //     student_id: '2018750',
      //     student_name: '阮啟东2',
      //     class_belong: '软件2017-01',
      //     lock_state: '已锁屏',
      //     every_register_sit: '1'
      //   },
      //
      //   { // 第三个学生的所有信息
      //     pri_pic: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3259856739,784554029&fm=26&gp=0.jpg',
      //     student_id: '2018750',
      //     student_name: '阮啟东23',
      //     class_belong: '软件2017-01',
      //     lock_state: '已锁屏',
      //     every_register_sit: '1'
      //   },
      //   { // 第三个学生的所有信息
      //     pri_pic: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3259856739,784554029&fm=26&gp=0.jpg',
      //     student_id: '2018750',
      //     student_name: '阮啟东23',
      //     class_belong: '软件2017-01',
      //     lock_state: '已锁屏',
      //     every_register_sit: '1'
      //   }
      //   ]
      // },
      chooseOptions: [{
        value: '4',
        label: '出勤'
      }, {
        value: '3',
        label: '迟到'
      }, {
        value: '2',
        label: '请假'
      }, {
        value: '1',
        label: '缺勤'
      }],
      chooseOptionTotalValue: ''

    }
  },
  methods: {
    modifyStudentRegisterSit (row) {
      console.log(this.user_info)
      axModule.get(this.GLOBAL.host + '/modifySingleStudentRegisterInfo', {
        params: {
          user_id: this.user_info.user.userId,
          user_password: this.user_info.user.userPassword,
          classNo: this.$store.state.storeCurrentClassNo,
          register_time: this.$store.state.storeCurrentRegisterTime,
          every_register_sit: row.situation,
          student_id: row.studentNumber
        }
      }).then(res => {
        console.log(res)
        if (res.status === 200) {
          // this.studentInfo = res.data
          // setTimeout(this.getAllStudentsRegister, 2000)
          this.$message({
            message: '修改成功',
            type: 'success'
          })
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



